@charset "UTF-8";

html,
body,
div,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
header,
main,
section,
article,
aside,
footer,
figure,
figcaption,
nav {
  margin: 0;
  padding: 0;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

html {
  /*
  overflow: scroll;
  overflow: -moz-scrollbars-vertical;
  overflow-x: scroll;
  */
}

ul,
ol {
  list-style: none;
}

p,
li,
dt,
dd,
th,
td,
pre {
  -ms-line-break: strict;
  line-break: strict;
  -ms-word-break: break-strict;
  word-break: break-strict;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  border: none;
  max-width: 100%;
  vertical-align: middle;
}

button {
  cursor: pointer;
  outline: none;
  border: none;
  background: none;
}

:root {
  --base-font-size: 16px;
  --base-color: #800080;
  --assort-color: #c0c;
  --white: #fff;
  --gray-lightest: #fafafa;
  --gray-lighter: #ccc;
  --gray-light: #999;
  --gray: #666;
  --gray-deep: #333;
  --black-light: #212121;
  --black: #000;
  --transparent: rgba(0, 0, 0, 0);
  --fieldset-width: 98%;
}

.text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.text-gradient {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image:
    linear-gradient(
      to bottom,
      var(--assort-color),
      rgba(#b200b2, 1) 55%,
      rgba(#a000a0, 1) 50%,
      rgba(#b700b7, 1)
    );
}

.image-link:hover,
.image-link:focus,
.image-link:active {
  text-decoration: none;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: var(--base-font-size);
  color: var(--gray);
  line-height: 1.5;
  min-width: 320px;
  background-color: var(--black);
}

body.-radial {
  background-image: -webkit-radial-gradient(50% 50%, circle contain, var(--gray-lighter), var(--transparent));
  background-image: -moz-radial-gradient(50% 50%, circle contain, var(--gray-lighter), var(--transparent));
}

body.-linear {
  background-image:
    linear-gradient(
      -45deg,
      var(--black),
      var(--gray-deep) 15%,
      var(--gray-lighter) 25%,
      var(--gray-light) 30%,
      var(--black) 40%,
      var(--black) 50%,
      var(--black)
    );
}

noscript {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  height: 100vh;
  font-family: "Alike", serif;
  font-size: var(--base-font-size);
  color: var(--assort-color);
}

.Application {
  margin: 0 auto;
  max-width: 1400px;
  min-width: 1280px;  /* for Piano width */
}

a {
  color: var(--assort-color);
  text-decoration: none;
  transition: color ease 0.6s;
}

a:hover {
  color: var(--base-color);
  text-decoration: underline;
}

[hidden] {
  display: none !important;
}

.Button {
  display: inline-block;

  /* CSS Sprite */
  background-position: left bottom;
  background-repeat: no-repeat;
}

.Button:hover {
  background-position: left top;
}

.Button:active,
.Button.-active {
  background-position: left center;
}

.Button.-active:active {
  background-position: left bottom;
}

.FileUploader > button,
.FileUploader.-drop > button {
  box-sizing: border-box;
  border: 2px solid rgba(255, 255, 255, 0.05);
  width: 200px;
  height: 28px;
  padding: 4px;
  font-size: 9px;
  color: var(--white);
  background-color: var(--black);
  background-image: linear-gradient(to bottom, var(--gray), var(--black));
  box-shadow: 0 0 0 1px var(--black) inset, 0 0 1px 3px rgba(0, 0, 0, 0.3);  /* outer (for emboss) */
}

.FileUploader > button:focus,
.FileUploader > button:active,
.FileUploader.-drag > button {
  border: 2px solid rgba(255, 255, 255, 0.05);
  background-color: var(--assort-color);
  background-image: linear-gradient(to bottom, var(--base-color), var(--assort-color));
  box-shadow: 0 0 0 1px var(--base-color) inset, 0 0 1px 3px rgba(0, 0, 0, 0.3)));  /* outer (for emboss) */
}

.Flexbox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-items: stretch;
  align-items: stretch;
}

.Modal {
  display: none;
}

.Modal.-show {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9997;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
}

.Modal__overlay {
  cursor: pointer;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

.Modal__inner {
  position: fixed;
  z-index: 9999;
  min-width: 280px;
  background-color: var(--black);
}

.Modal__title {
  padding: 8px;
  font-size: 18px;
  font-weight: normal;
  color: var(--white);
  background-color: var(--assort-color);
}

.Modal__contents {
  margin: 36px 8px;
  color: var(--assort-color);
}

.ProgressBar__wrapper {
  position: relative;
  z-index: 1;
  margin: 12px auto;
  width: 204px;
  height: 12px;
  background-color: var(--base-color);
  background-image: linear-gradient(to bottom, var(--base-color), var(--assort-color));
}

.ProgressBar__mask {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 204px;
  height: 6px;
  background-color: rgba(255, 255, 255, 0.15);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), var(--transparent));
}

.ProgressBar__bar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 0;
  height: 12px;
  background-color: var(--assort-color);
}

.ProgressBar__bar.-auto {
  animation: auto-progress-animation 3s ease-in-out infinite normal 0s;
}

@keyframes auto-progress-animation {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

.ProgressBar__title {
  font-family: "Alike", serif;
  font-size: var(--base-font-size);
  color: var(--assort-color);
  text-align: center;
}

.Select {
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  display: block;
  margin: 0 auto;
  border: 1px solid rgba(255, 255, 255, 0.05);
  padding: 8px;
  width: 95%;
  font-size: 13px;
  color: var(--white);
  border-radius: none;
  box-shadow: 0 0 0 2px var(--black) inset, 0 -1px 1px 2px rgba(0, 0, 0, 0.5);
  background-image: linear-gradient(to bottom, var(--gray), var(--black));
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}

.Select:focus {
  outline: none;
  background-image: linear-gradient(to bottom, var(--assort-color), var(--base-color));
}

.Slider {
  position: relative;
  z-index: 1;
  margin: 0 auto;
}

.Slider > input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  width: 97%;
  height: 4px;
  background-color: var(--white);
  background-image: linear-gradient(to bottom, var(--gray-light), var(--black));
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: var(--transparent);
}

.Slider > input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  border: none;
  width: 23px;
  height: 23px;
  background-color: var(--transparent);

  /* CSS Sprite */
  background-image: url(/assets/images/slider-handles.png);
  background-size: 23px 46px;
  background-repeat: no-repeat;
  background-position: 0 -23px;
}

.Slider > input[type="range"]::-moz-range-thumb {
  -moz-appearance: none;
  cursor: pointer;
  border: none;
  width: 23px;
  height: 23px;
  background-color: var(--transparent);

  /* CSS Sprite */
  background-image: url(/assets/images/slider-handles.png);
  background-size: 23px 46px;
  background-repeat: no-repeat;
  background-position: 0 -23px;
}

.Slider > input[type="range"]:active::-webkit-slider-thumb {
  cursor: ew-resize;
  background-position: 0 0;
}

.Slider > input[type="range"]:active::-moz-range-thumb {
  cursor: ew-resize;
  background-position: 0 0;
}

.Slider__value {
  position: absolute;
  top: 11.5px;
  left: 4px;
  z-index: 2;
  max-width: calc(100% - 28px);
  width: 0;
  height: 4px;
  background: #d7d7d7;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5) inset;
  border-radius: 0 3px 3px 0;
}

@-moz-document url-prefix() {
  .Slider__value {
    top: 8px;
  }
}

.Spinner {
  cursor: pointer;
  display: inline-block;
  outline: none;
  border: 2px solid rgba(255, 255, 255, 0.05);
  padding: 4px 0 4px 8px;
  width: 40%;
  font-size: 13px;
  color: var(--white);
  background-color: var(--black);
  background-image: linear-gradient(to bottom, var(--gray-deep), var(--black));
  box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.8) inset, 3px -3px 3px rgba(0, 0, 0, 0.5) inset, 0 0 0 2px var(--black) inset, 0 -1px 1px 2px rgba(0, 0, 0, 0.5);  /* outer (for emboss) */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: var(--transparent);
}

.Spinner:focus,
.Spinner:active {
  background-color: var(--base-color);
  background-image: linear-gradient(to bottom, var(--base-color), var(--assort-color));
  box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.4) inset, 3px -3px 3px rgba(0, 0, 0, 0.2) inset, 0 -1px 5px 1px rgba(17, 17, 17, 0.75);  /* outer (for emboss) */
}

.Switch > label {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
  padding: 0 4px;
}

.Switch > label > span {
  cursor: pointer;
  display: inline-block;
  margin-left: 12px;
  vertical-align: -12px;
  width: 58px;
  height: 34px;

  /* for Retina Display */
  background-size: 58px 68px;

  /* CSS Sprite */
  background-image: url(/assets/images/flip-switch.png);
  background-position: left top;
  background-repeat: no-repeat;
}

.Switch.-checked > label > span {
  background-position: left bottom;
}

.VerticalBox {
  width: calc(100% / 6);
}

